import {
  mobile as mobileBreakpoint,
  tablet as tabletBreakpoint,
  laptop as laptopBreakpoint,
  desktop as desktopBreakpoint,
} from '#theme/breakPoints';

const size = (w: number, ratio = 16 / 9) => ({
  width: `${w}px`,
  height: `${Math.round(w * ratio)}px`,
});

export const mobile = {
  name: 'Mobile',
  styles: size(mobileBreakpoint),
  type: 'mobile',
};

export const tablet = {
  name: 'Tablet',
  styles: size(tabletBreakpoint, 4 / 3),
  type: 'tablet',
};

export const laptop = {
  name: 'Laptop',
  styles: size(laptopBreakpoint, 10 / 16),
  type: 'desktop',
};

export const desktop = {
  name: 'Desktop',
  styles: size(desktopBreakpoint, 10 / 16),
  type: 'desktop',
};
